.popup__close {
    position: absolute;
    top: -40px;
    right: -40px;
    padding: 0;
    display: flex;
    background-image: url(../../../images/add-button.svg);
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transform: rotate(45deg);
    border: none;
    background-color: transparent;
    transition: .2s;
    outline: none;
  }

  .popup__close:hover {
    opacity: .6;
    cursor: pointer;
  }

  @media screen and (max-width: 620px) {
    .popup__close {
      width: 20px;
      height: 20px;
      top: -50px;
      right: 0;
    }
  }
